

import {TOKEN_EXPIRE_TIME, TOKEN_EXPIRE_TIME_VALUE} from './constant'

export const get_token_time = () => {
    return localStorage.getItem(TOKEN_EXPIRE_TIME)
}

export const set_token_time = () => {
    localStorage.setItem(TOKEN_EXPIRE_TIME, String(Date.now()))
}

export const is_token_expire = () => {
    return Date.now() - Number(get_token_time()) > TOKEN_EXPIRE_TIME_VALUE ? true : false;
}




