import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router'

import layout from '@/layout/Index.vue'
import Aaaa from '@/layout/aaaa.vue'

const routes: Array<RouteRecordRaw> = [
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/loginregister/LoginRegister.vue')
  },
  {
    path: '/aaaa',
    name: 'aaaa',
    component: Aaaa,
    children: [
      {
        path: '/cccc',
        name: 'cccc',
        component: () => import('../views/Ca.vue'),
        props: ["foo"],
      },
    ]
  },
  {
    path: '/',
    name: 'Index',
    component: layout,
    redirect: '/task',
    children: [
      {
        path: '/task',
        name: 'Task',
        component: () => import('../views/task/Task.vue'),
      },
      {
        path: '/detail',
        name: 'Detail',
        component: () => import('../views/detail/Detail.vue'),
      },
      {
        path: '/gitdetail',
        name: 'GitDetail',
        component: () => import('../views/git/detail/Detail.vue'),
      },
      {
        path: '/verify',
        name: 'Verify',
        component: () => import('../views/verify/Verify.vue') 
      },
      {
        path: '/search',
        name: 'Search',
        component: () => import('../views/search/Search.vue') 
      },
      {
        path: '/poc',
        name: 'Poc',
        component: () => import('../views/pocmanage/PocManage.vue') 
      },
      {
        path: '/settings',
        name: 'Settings',
        component: () => import('../views/settings/Settings.vue') 
      },
      {
        path: '/git',
        name: 'Git',
        component: () => import('../views/git/Git.vue') 
      },
      // {
      //   path: '/log',
      //   name: 'Log',
      //   component: () => import('../views/log/OperaLog.vue') 
      // },
    ]
  },
  {
    path: '/:catchAll(.*)',
    name: '404',
    component: () => import('../views/404.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
