import axios, { AxiosRequestConfig, AxiosResponse } from 'axios'
import { ElLoading, ElMessage} from 'element-plus'
import {is_token_expire} from '@/utils/auth'
import store from '../store'
import router from '../router'

// axios参考文章：https://www.axios-http.cn/docs/interceptors
// https://element-plus.gitee.io/zh-CN/component/loading.html#%E8%AE%A9%E5%8A%A0%E8%BD%BD%E7%BB%84%E4%BB%B6%E9%93%BA%E6%BB%A1%E6%95%B4%E4%B8%AA%E5%B1%8F%E5%B9%95
let loading: any

const start_load = () => {
  interface Options {
    lock: boolean
    text: string
    background: string
  }
  const options: Options = {
    lock: true,
    text: 'Loading',
    background: 'rgba(0,0,0,0.7)'
  }
  loading = ElLoading.service(options)
}

const end_load = () =>{ 
  loading.close()
}


const service = axios.create({
  // @ts-ignore
  baseURL: process.env.VUE_APP_BASE_API,
  timeout: 5000
})

// 添加请求拦截器
service.interceptors.request.use(
  (config: AxiosRequestConfig) => {
    // 在发送请求之前做些什么
    start_load()
    // @ts-ignore
    config.headers.Authorization = `Bearer ${localStorage.getItem('access_token')}`
    if(localStorage.getItem('access_token')){
      if(is_token_expire()){
        store.dispatch('app/logout')
        router.push({ path: '/login', replace: true })
        return Promise.reject(new Error("access_token过期.."))
      }
    }
    
    return config
  },
  (erorr) => {
    // 对请求错误做些什么
    return Promise.reject(new Error("请求错误..."))
  }
)

// 添加响应拦截器
service.interceptors.response.use(
  (response:AxiosResponse<any>) => {
    // 2xx 范围内的状态码都会触发该函数。
    // 对响应数据做点什么
    end_load()
    const {message, status} = response.data
    return response
    // if(status){
    //   return response
    // }else{
    //   !status && ElMessage.error(message)
    // }
    // return response
  },

  (erorr) => {
    // 超出 2xx 范围的状态码都会触发该函数。
    // 对响应错误做点什么
    end_load()
    return Promise.reject(erorr)
  }
)


export default service
