import { createApp } from "vue";
import App from "./App.vue";
// router
import router from "./router";
import "@/router/permission";
// store
import store from "./store";
// css
import "@/styles/index.scss";
// element plus
import ElementPlus from "element-plus";
import "element-plus/dist/index.css";
// axios
import service from "./utils/http";
// plus-icon - local register
import SvgIcon from "@/icons";
// plus-icon - global register
import * as ELIcons from "@element-plus/icons-vue";
import common from "@/utils/common";

// 创建实例
const app = createApp(App);

// 原型链使其支持 [...Number]
Number.prototype[Symbol.iterator] = function* () {
  let i = 0;
  let num = this.valueOf();
  while (i < num) {
    yield i++;
  }
};

// 注册全局 common 函数 -> utils/common.ts
common(app);

// 注册所有组件操作
// 参考：https://element-plus.gitee.io/zh-CN/component/icon.html#%E6%B3%A8%E5%86%8C%E6%89%80%E6%9C%89%E5%9B%BE%E6%A0%87
for (const icon_name in ELIcons) {
  app.component(icon_name, ELIcons[icon_name]);
}

// 注册局部组件 /icons目录下的svg
SvgIcon(app);
//about chain
app
  .use(store)
  .use(router)
  .use(ElementPlus)
  .provide("axios", service)
  .mount("#app");
