const dayjs = require("dayjs");

// 参考：https://github.com/iamkun/dayjs
export const trans_time2date = (val, format = "YYYY-MM-DD HH:mm:ss") => {
  if (!is_null(val)) {
    return dayjs(parseInt(val) * 1000).format(format);
  } else {
    return "-";
  }
};

export const is_null = (val) => {
  if (!val) {
    return true;
  }
  // @ts-ignore
  if (JSON.stringify(val) == {}) return true;
  // @ts-ignore
  if (JSON.stringify(val) == []) return true;

  return false;
};

export default (app) => {
  app.config.globalProperties.$commons = {
    trans_time2date,
    is_null,
  };
};
