import { set_token_time } from '@/utils/auth'

export default {
  namespaced: true,
  state: () => ({
    access_token: localStorage.getItem('access_token'),
    sidebar_type: true,
  }),
  mutations: {
    setToken(state, access_token) {
      state.access_token = access_token
      localStorage.setItem('access_token', access_token)
      set_token_time()
    },
    change_sidebar_type(state){
      state.sidebar_type = !state.sidebar_type
    }
  },
  actions: {
    login({commit}, access_token){
      commit('setToken', access_token)
    },
    logout({commit}){
      commit('setToken', '')
      localStorage.clear()
    },
  }
}
