import SvgIcon from '@/components/SvgIcon.vue'

// 参考文章：https://webpack.docschina.org/guides/dependency-management/

// Webpack的require.context查找svg目录下的所有匹配.svg的svg文件
const svg_required = require.context('./svg', false, /\.svg$/)

// 遍历svg
svg_required.keys().forEach((item) => svg_required(item))
  
// 导入svg-icon标签
export default (app) => {
  app.component('svg-icon', SvgIcon)
}
