import router from "./index";
import store from "@/store/index";

// 特殊情况下有些页面有可能也可以访问，这边给个白名单进行记录下

const white_lists = ["/login"]; // , '/bbbb'

router.beforeEach((to, from, next) => {
  // 有权限的情况下
  if (store.getters.access_token) {
    if (to.path == "/login") {
      next("/");
    } else {
      next();
    }
  } else {
    // 没有权限的情况下
    if (white_lists.includes(to.path)){
        next()
    }else{
        next('/login')
    }
  }
});
